import {useCallback} from 'react';
import useBnbmFinance from './useBnbmFinance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';
import {parseUnits} from 'ethers/lib/utils';
import {TAX_OFFICE_ADDR} from '../utils/constants';

const useProvideBnbmFtmLP = () => {
  const bnbmFinance = useBnbmFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleProvideBnbmFtmLP = useCallback(
    (ftmAmount: string, bnbmAmount: string) => {
      const bnbmAmountBn = parseUnits(bnbmAmount);
      handleTransactionReceipt(
        bnbmFinance.provideBnbmFtmLP(ftmAmount, bnbmAmountBn),
        `Provide BNBM-BTCB LP ${bnbmAmount} ${ftmAmount} using ${TAX_OFFICE_ADDR}`,
      );
    },
    [bnbmFinance, handleTransactionReceipt],
  );
  return {onProvideBnbmFtmLP: handleProvideBnbmFtmLP};
};

export default useProvideBnbmFtmLP;
