import React, { useMemo, useState } from 'react';
import Page from '../../components/Page';
import useLpStats from '../../hooks/useLpStats';
import { Box, Button, Grid, Paper, Typography } from '@material-ui/core';
import useBnbmStats from '../../hooks/useBondStats';
import TokenInput from '../../components/TokenInput';
import useBnbmFinance from '../../hooks/useBnbmFinance';
import { useWallet } from 'use-wallet';
import useTokenBalance from '../../hooks/useTokenBalance';
import { getDisplayBalance } from '../../utils/formatBalance';
import useApproveTaxOffice from '../../hooks/useApproveTaxOffice';
import { ApprovalState } from '../../hooks/useApprove';
import useProvideBnbmFtmLP from '../../hooks/useProvideBnbmFtmLP';
import { Alert } from '@material-ui/lab';
import { Helmet } from 'react-helmet';
import {BackgroundImage} from '../Home/Home.js';

function isNumeric(n) {
  return !isNaN(parseFloat(n)) && isFinite(n);
}
const TITLE = 'bnbm.finance |'

const ProvideLiquidity = () => {
  const [bnbmAmount, setBnbmAmount] = useState(0);
  const [ftmAmount, setFtmAmount] = useState(0);
  const [lpTokensAmount, setLpTokensAmount] = useState(0);
  const { balance } = useWallet();
  const bnbmStats = useBnbmStats();
  const bnbmFinance = useBnbmFinance();
  const [approveTaxOfficeStatus, approveTaxOffice] = useApproveTaxOffice();
  const bnbmBalance = useTokenBalance(bnbmFinance.BNBM);
  const btcBalance = useTokenBalance(bnbmFinance.BTC);

  const ftmBalance = (btcBalance / 1e18).toFixed(4);
  const { onProvideBnbmFtmLP } = useProvideBnbmFtmLP();
  const bnbmFtmLpStats = useLpStats('BNBM-BNB-LP');

  const bnbmLPStats = useMemo(() => (bnbmFtmLpStats ? bnbmFtmLpStats : null), [bnbmFtmLpStats]);
  const bnbmPriceInBNB = useMemo(() => (bnbmStats ? Number(bnbmStats.tokenInFtm).toFixed(2) : null), [bnbmStats]);
  const ftmPriceInBNBM = useMemo(() => (bnbmStats ? Number(1 / bnbmStats.tokenInFtm).toFixed(2) : null), [bnbmStats]);
  // const classes = useStyles();

  const handleBnbmChange = async (e) => {
    if (e.currentTarget.value === '' || e.currentTarget.value === 0) {
      setBnbmAmount(e.currentTarget.value);
    }
    if (!isNumeric(e.currentTarget.value)) return;
    setBnbmAmount(e.currentTarget.value);
    const quoteFromSpooky = await bnbmFinance.quoteFromSpooky(e.currentTarget.value, 'BNBM');
    setFtmAmount(quoteFromSpooky);
    setLpTokensAmount(quoteFromSpooky / bnbmLPStats.ftmAmount);
  };

  const handleFtmChange = async (e) => {
    if (e.currentTarget.value === '' || e.currentTarget.value === 0) {
      setFtmAmount(e.currentTarget.value);
    }
    if (!isNumeric(e.currentTarget.value)) return;
    setFtmAmount(e.currentTarget.value);
    const quoteFromSpooky = await bnbmFinance.quoteFromSpooky(e.currentTarget.value, 'BTCB');
    setBnbmAmount(quoteFromSpooky);

    setLpTokensAmount(quoteFromSpooky / bnbmLPStats.tokenAmount);
  };
  const handleBnbmSelectMax = async () => {
    const quoteFromSpooky = await bnbmFinance.quoteFromSpooky(getDisplayBalance(bnbmBalance), 'BNBM');
    setBnbmAmount(getDisplayBalance(bnbmBalance));
    setFtmAmount(quoteFromSpooky);
    setLpTokensAmount(quoteFromSpooky / bnbmLPStats.ftmAmount);
  };
  const handleFtmSelectMax = async () => {
    const quoteFromSpooky = await bnbmFinance.quoteFromSpooky(ftmBalance, 'BNB');
    setFtmAmount(ftmBalance);
    setBnbmAmount(quoteFromSpooky);
    setLpTokensAmount(ftmBalance / bnbmLPStats.ftmAmount);
  };
  return (

    <Page>
      <Helmet>
        <title>{TITLE}</title>
      </Helmet>
      <BackgroundImage />
      <Typography color="textPrimary" align="center" variant="h3" gutterBottom>
        Provide Liquidity
      </Typography>

      <Grid container justify="center">
        <Box style={{ width: '600px' }}>
          <Alert variant="filled" severity="warning" style={{ marginBottom: '10px' }}>
            <b>
              This and{' '}
              <a href="https://pancakeswap.finance/" rel="noopener noreferrer" target="_blank">
                Pancakeswap
              </a>{' '}
              are the only ways to provide Liquidity on BNBM-BTCB pair without paying tax.
            </b>
          </Alert>
          <Grid item xs={12} sm={12}>
            <Paper>
              <Box mt={4}>
                <Grid item xs={12} sm={12} style={{ borderRadius: 15 }}>
                  <Box p={4}>
                    <Grid container>
                      <Grid item xs={12}>
                        <TokenInput
                          onSelectMax={handleBnbmSelectMax}
                          onChange={handleBnbmChange}
                          value={bnbmAmount}
                          max={getDisplayBalance(bnbmBalance)}
                          symbol={'BNBM'}
                        ></TokenInput>
                      </Grid>
                      <Grid item xs={12}>
                        <TokenInput
                          onSelectMax={handleFtmSelectMax}
                          onChange={handleFtmChange}
                          value={ftmAmount}
                          max={ftmBalance}
                          symbol={'BTCB'}
                        ></TokenInput>
                      </Grid>
                      <Grid item xs={12}>
                        <p>1 BNBM = {bnbmPriceInBNB} BNB</p>
                        <p>1 BNB = {ftmPriceInBNBM} BNBM</p>
                        <p>LP tokens â‰ˆ {lpTokensAmount.toFixed(2)}</p>
                      </Grid>
                      <Grid xs={12} justifyContent="center" style={{ textAlign: 'center' }}>
                        {approveTaxOfficeStatus === ApprovalState.APPROVED ? (
                          <Button
                            variant="contained"
                            onClick={() => onProvideBnbmFtmLP(ftmAmount.toString(), bnbmAmount.toString())}
                            color="primary"
                            style={{ margin: '0 10px', color: '#fff' }}
                          >
                            Supply
                          </Button>
                        ) : (
                          <Button
                            variant="contained"
                            onClick={() => approveTaxOffice()}
                            color="secondary"
                            style={{ margin: '0 10px' }}
                          >
                            Approve
                          </Button>
                        )}
                      </Grid>
                    </Grid>
                  </Box>
                </Grid>
              </Box>
            </Paper>
          </Grid>
        </Box>
      </Grid>
    </Page>
  );
};

export default ProvideLiquidity;
